body {
    background-color: black;
    margin: 20px;
}

.hide {
    visibility: hidden;
}
  
.show {
    visibility: visible;
}

.carousel-main {
    display: flex;
    flex-direction: column;
}
  
.category-title {
    color: white;
    padding-left: 25px;
    margin: 0;
}

.carousel-content {
    display: flex;
    margin: 0 15px;
    overflow-x: hidden;
    scroll-behavior: smooth;
}

.carousel-content > :last-child {
    margin-right: 0;
}
  
.image {
    margin-bottom: 20px;
    width: 300px;
    height: 150px;
    border-style:solid;
    background-size: 300px 150px;
    white-space: nowrap;

}

.image:focus {
    outline-color: white;
    border-color:white;
    background-color: white;
    transform: scale(1.1);
}

.image:active {
    outline-color: green;
    border-color: green;
    background-color: green;
}
  
.image-center {
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 300px;
    height: 150px;
}

.show-title {
    color: white;
    font-size: 18px;
    font-weight: 200;
    margin-right: 20px;
}

.show-content {
    margin: 20px;
}

.text {
    color:white;
}

.sub-text{
    text-align: left;
    padding-top: 20px;
    font-size: 15px;
  }

.text-small {
    text-align: left;
    font-size: 14px;
}

.modal {
    position: fixed; 
    z-index: 1; 
    padding-top: 100px; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: auto; 
    background-color: rgb(0,0,0); 
    background-color: rgba(0,0,0,0.4); 
}
  
.modal-content {
    background-color: black;
    margin: auto;
    padding: 20px;
    border: 1px solid #888;
    width: 50%;
}

.modal-text{
    text-align: left;
    padding-top: 20px;
    font-size: 24px;
    font-weight: 500;
    line-height: 30px;
  }
  
.close {
    color: white;
    background-color: black;
    border: black;
    float: right;
    font-size: 28px;
    font-weight: bold;
}
  
.close:hover,
.close:focus {
    color: green;
    text-decoration: none;
    cursor: pointer;
}

li {
    display: inline-block;
    list-style: none;
    margin: 0 auto;
    height: 14px;
    }
    ul li::after {
        content: " | ";
        margin: 0 10px;
    }
    ul li:last-child:after {
        content: '';
        margin: 0 10px;
    }
    ul {
        padding: 0;
        margin: 0;
    }
